  margin-bottom: 0.75rem;
  color: var(--foreground);
  line-height: 1.3;
}
.story-excerpt {
  font-size: 0.95rem;
  color: var(--foreground);
  opacity: 0.8;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}
.story-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 1rem;
  border-top: 1px solid var(--glass-border);
}
.author-info {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}
.author-avatar {
  font-size: 1.5rem;
}
.author-name {
  font-weight: 600;
  color: var(--foreground);
}
.story-stats {
  display: flex;
  gap: 1rem;
}
.stat {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 0.9rem;
  color: var(--foreground);
  opacity: 0.8;
}
.stat-icon {
  font-size: 1rem;
}
.story-page {
  min-height: 100vh;
  padding: 2rem;
  animation: fadeIn 0.6s ease-out;
}
.story-container {
  max-width: 900px;
  margin: 0 auto;
}
.back-button {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
